ducativas, lo que los hace más versátiles y accesibles a una audiencia más amplia de educadores y estudiantes. Esto reduce las barreras técnicas para la adopción y el uso de recursos digitales.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Portabilidad"}},[_c('p',[_vm._v("Los RED deben ser accesibles a través de diferentes dispositivos y sistemas operativos, lo que asegura que los estudiantes puedan utilizar los RED sin importar el dispositivo que emplee. Esto pone de relieve la importancia de un diseño responsivo y la compatibilidad multiplataforma. En el actual panorama tecnológico, los estudiantes emplean diversos dispositivos para acceder a contenido en línea. Los RED portátiles aseguran que los materiales de aprendizaje sean accesibles en computadoras de escritorio, portátiles, tabletas y teléfonos inteligentes, ofreciendo una experiencia de aprendizaje coherente y conveniente.")])])])],1)]),_c('p',{staticClass:"mb-5"},[_vm._v("La siguiente tabla resume las características claves de los Recursos Educativos Digitales:")]),_vm._m(5),_vm._m(6),_vm._m(7),_c('p',{staticClass:"mb-5"},[_vm._v("La diversidad de los Recursos Educativos Digitales (RED), permite a los educadores seleccionar y adaptar aquellos que mejor se ajustan a sus objetivos pedagógicos y a las necesidades de sus estudiantes. Los RED pueden clasificarse en función de su formato, su nivel de interactividad y su enfoque pedagógico. En esta sección, exploramos tres categorías principales: ")]),_c('div',{staticClass:"row align-items-start mb-5"},[_vm._m(8),_c('div',{staticClass:"col-lg-8 order-lg-1"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Los recursos interactivos"}},[_c('p',[_vm._v("Son aquellos que requieren la participación activa del estudiante para acceder al contenido o para completar una tarea. Estos recursos fomentan un aprendizaje más profundo al involucrar cognitivamente al estudiante, en el proceso de construcción del conocimiento. ")]),_c('p',[_vm._v("Ejemplos de recursos interactivos incluyen simulaciones virtuales que permiten a los estudiantes experimentar con conceptos abstractos en un entorno seguro, cuestionarios en línea con retroalimentación inmediata que ayudan a los estudiantes a autoevaluar su comprensión, y actividades de arrastrar y soltar que requieren la manipulación de elementos para demostrar el aprendizaje. Plataformas como H5P y Genially, ofrecen una amplia gama de herramientas para crear recursos interactivos sin necesidad de conocimientos avanzados de programación.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Los recursos multimedia"}},[_c('p',[_vm._v("Integran diferentes formatos de contenido como texto, imágenes, audio, video y animaciones, para presentar la información de una manera más atractiva y accesible. La combinación de estos formatos puede ayudar a los estudiantes con diferentes estilos de aprendizaje, a comprender mejor los conceptos y a retener la información, de manera más efectiva. ")]),_c('p',[_vm._v("Ejemplos de recursos multimedia incluyen videos explicativos que combinan imágenes y narración para ilustrar un tema, presentaciones interactivas que incorporan elementos visuales y auditivos, y pódcasts educativos que permiten a los estudiantes aprender mientras realizan otras actividades. YouTube 43 y Vimeo, son plataformas populares para encontrar y compartir recursos multimedia educativos.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Los recursos gamificados"}},[_c('p',[_vm._v("Aplican elementos y principios del diseño de juegos en contextos educativos, para aumentar la motivación y el compromiso de los estudiantes. Estos recursos buscan transformar la experiencia de aprendizaje haciéndola más divertida y desafiante a través de la incorporación de elementos como puntos, insignias, tablas de clasificación, desafíos y recompensas. La gamificación puede fomentar la participación activa, la resolución de problemas y el aprendizaje colaborativo en este sentido. 